a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,canvas,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video
{
margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline
}

article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section
{
display:block
}

body
{
line-height:1
}

ol,ul
{
list-style:none
}

blockquote,q
{
quotes:none
}

blockquote:after,blockquote:before
{
content:'';content:none
}

q:after,q:before
{
content:'';content:none
}

body
{
width:100%
}

body:after,body:before
{
content:"";display:table
}

body:after
{
clear:both
}

body,html
{
height:100%;overflow-x:hidden
}

html
{
height:100%;max-height:100%
}

body
{
font-family:Raleway,sans-serif;font-size:1em;color:#666
}

::selection
{
background:#fae3df
}

::-moz-selection
{
background:#fae3df
}

a
{
text-decoration:none;color:#000
}

.citation:focus,.citation:hover,a:focus,a:hover
{
animation-duration:5s;animation-name:rainbowLink;animation-iteration-count:infinite
}

@keyframes rainbowLink
{
0%
{
color:#ff2a2a
}

15%
{
color:#ff7a2a
}

30%
{
color:#ffc52a
}

45%
{
color:#43ff2a
}

60%
{
color:#2a89ff
}

75%
{
color:#202082
}

90%
{
color:#6b2aff
}

100%
{
color:#e82aff
}


}

h1
{
margin-top:.8em;margin-bottom:.4em;font-family:"Roboto Slab",serif;font-weight:lighter;color:#333;-webkit-font-smoothing:antialiased;font-size:3.2em;line-height:1.2em;letter-spacing:.05em
}

h2
{
