}

h5
{
font-weight:700;-webkit-font-smoothing:antialiased
}

p
{
margin-bottom:1.3em;line-height:1.7em
}

strong
{
font-weight:700
}

em
{
font-style:italic
}

ul
{
margin:0 0 1.3em 2.5em
}

ul li
