}

.label
{
position:relative;display:inline-block;padding:8px 18px 9px 18px;background:#e25440;border-radius:3px;text-align:center;color:#fff
}

.content-wrapper
{
z-index:800;width:60%;margin-left:40%
}

.content-wrapper__inner
{
max-width:100%
}

.footer
{
display:block;padding:2em 0 0 0;border-top:2px solid #ddd;font-size:.7em;color:#b3b3b3
}

.footer__copyright
{
display:block;margin-bottom:.7em
}

hr
{
border:none
}

.hidden
{
display:none!important
}

.panel-title
{
margin:0 0 5px 0;font-size:2.5em;letter-spacing:4px;color:#fff
}

.panel-cover
{
display:block;position:fixed;z-index:900;width:100%;max-width:none;height:100%;background:url(images/background.jpg) top left no-repeat #666;background-size:cover
}

.panel-cover--collapsed
{
